export class Carousel {
    constructor(slidesClass, leftButtonSelector, rightButtonSelector) {
        this.slidePosition = 0;
        this.slides = document.getElementsByClassName(slidesClass);
        this.totalSlides = this.slides.length;

        this.leftButton = document.querySelector(leftButtonSelector);
        this.rightButton = document.querySelector(rightButtonSelector);

        this.leftButton.addEventListener('click', () => this.move(-1));
        this.rightButton.addEventListener('click', () => this.move(1));
    }

    updateSlidePosition() {
        for (let slide of this.slides) {
            slide.classList.remove('carousel__slide_type_current');
            slide.classList.add('carousel__slide_type_hidden');
        }

        this.slides[this.slidePosition].classList.add('carousel__slide_type_current');
    }

    move(n) {
        this.slidePosition += n;
        if (this.slidePosition < 0) {
            this.slidePosition = this.totalSlides - 1;
        } else if (this.slidePosition === this.totalSlides) {
            this.slidePosition = 0;
        }
        console.log('it works');
        this.updateSlidePosition();
    }
}